// Functions
@function calculateRem($size) {
    $vwSize: $size / 4px;
    @return $vwSize * 1vw;
  }

/*-- Margin Top --*/
@for $i from 1 through 40 {
    .mt-#{5 * $i} {
      margin-top: 5px * $i;
    }
  }
  
  /*-- Margin Bottom --*/
  @for $i from 1 through 40 {
    .mb-#{5 * $i} {
      margin-bottom: 5px * $i;
    }
  }
  
  /*-- Margin Left --*/
  @for $i from 1 through 40 {
    .ml-#{5 * $i} {
      margin-left: 5px * $i;
    }
  }
  
  /*-- Margin Right --*/
  @for $i from 1 through 40 {
    .mr-#{5 * $i} {
      margin-right: 5px * $i;
    }
  }
  
  /*-- Padding Top --*/
  @for $i from 1 through 40 {
    .pt-#{5 * $i} {
      padding-top: 5px * $i;
    }
  }
  
  /*-- Padding Bottom --*/
  @for $i from 1 through 40 {
    .pb-#{5 * $i} {
      padding-bottom: 5px * $i;
    }
  }
  
  /*-- Padding Left --*/
  @for $i from 1 through 40 {
    .pl-#{5 * $i} {
      padding-left: 5px * $i;
    }
  }
  
  /*-- Padding Right --*/
  @for $i from 1 through 40 {
    .pr-#{5 * $i} {
      padding-right: 5px * $i;
    }
  }

  @for $i from 1 through 200 {
    .fs-#{1 * $i} {
      font-size: 1px * $i;
    }
  }